import React, { useEffect } from "react"
import { useLocation } from "react-router"
import { useDispatch } from "react-redux";
import queryString from "query-string";
import cookie from "js-cookie";

import { reqLoginVerification } from "../../../redux/actions"
import Loader from "../../components/Common/Loader";

const VerifyLoginReq = () => {
    const { search } = useLocation()
    const dispatch = useDispatch();
    useEffect(() => {
        const parsed: any = queryString.parse(search);
        localStorage.setItem("token", `Bearer ${parsed.token}`);
        cookie.set("token", `Bearer ${parsed.token}`, { expires: 1, path: '/', domain: process.env.REACT_APP_DOMAIN_NAME });
        dispatch(reqLoginVerification({
            redirectTo:parsed.redirectionUrl
        }))
        // eslint-disable-next-line
    }, [search])
    return <><Loader /></>
}

export default VerifyLoginReq